.search-container{
    display: flex;
    align-items: center;
    background-color: whitesmoke;
    border:none;
    border-radius: 10px;
    padding: 10px;
    margin-bottom: 20px;
}

.search-container input{
    background-color: whitesmoke;
    border:none;
}

.search-container input:focus{
    outline:none;
}